* {
  box-sizing: border-box;
}


html {
	overflow-x: hidden;
}

body {
    font-family: 'Arial';
    background-color: #272727;
	margin: 0;
  	box-sizing: border-box;
  	overflow-x: hidden;
}


.container {
	max-width: 540px;
	margin: 0 auto;
	padding: 0 15px;
}

.header {
	display: flex;
    z-index: 10000;
    background-color: #272727;
    height: 55px;
    border: none;
    margin-bottom: 0px;
    width: 100%;
    padding: 0px;
    position: fixed;
}

.ted {
    display: block;
    margin: 0 auto;
    margin-top: 0px;
    margin-bottom: 0px;
    font-size: 1.2rem;
    color: #fff;
    font-weight: 500;
    text-align: center;
    line-height: 55px;
}

.lined {
    background-color: #2e2e2e;
    width: 92%;
    margin: 0 auto;
    height: 1px;
    margin-bottom: 20px;
}



.main {
    background-color: #272727;
}

.obl {
    background-image: url(../img/obl.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    filter: blur(10px);
    position: fixed;
    z-index: 3000;
}

.bg {
    background: rgba(0,0,0,.6);
    position: fixed;
    z-index: 4000;
    width: 100%;
    height: 100%;
    opacity: .7;
}

.mainobl {
    position: fixed;
    z-index: 5000;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.gl {
    width: 450px;
    margin-top: 0px;
    margin-bottom: 0px;
}

.block {
    display: flex;
    justify-content: center;
    width: 450px;
    background-color: #aeb7c2;
    margin-top: -4px;
    margin-bottom: 0px;
    padding: 10px;
}

.vk{
    cursor: pointer;
    width: 200px;

}

.block p {
    margin-left: 10px;
    color: #000;
    font-size: 14px;
}

.d {
    padding: 0px;
    margin: 0px;
}
@media (max-width: 474px) {
    .gl {
        width: 300px;
        height: auto;
    }
    .block {
        width: 300px;        
    }
    .vk {
        width: 200px;
        height: 60px;
    }
    .block p {
        display: none;
    }
    .ted {
        text-align: left;
        margin-left: 30px;
    }
}

@media (max-width: 315px) {
    .gl {
        width: 250px;
        height: auto;
    }
    .block {
        width: 250px;        
    }
    .vk {
        width: 150px;
        height: 50px;
    }
}

